@import '@/styles/public.scss';

.addTask-box {
  .content-box {
    .list-item {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;

      .items-box {
        flex: 1;
        display: flex;
        .title {
          width: 120px;
          text-align: right;
          line-height: 28px;
          .tips {
            color: red;
          }
        }
        .content {
          line-height: 28px;
        }
        .conten {
          width: 76%;
          .tabs-content {
            padding-top: 10px;
            display: flex;
            .title-box {
              margin-right: 15px;
              .tips {
                color: red;
              }
            }
            .select-box {
              width: 150px;
            }
          }
          .custom-box {
            padding-top: 10px;
            .item-box {
              display: flex;
              margin-top: 10px;
              .title-box {
                width: 80px;
              }
              .item-content-box {
                flex: 1;
                display: flex;
                .time-box {
                  width: 180px;
                  /deep/ .el-date-editor.el-input, .el-date-editor.el-input__inner {
                    width: 100%;
                  }
                }
                .text-box {
                  line-height: 28px;
                  margin: 0 20px;
                }
                .icon-box {
                  width: 28px;
                  height: 28px;
                  margin-left: 20px;
                  .icon {
                    font-size: 20px;
                    line-height: 28px;
                    color: #4b77be;
                    cursor: pointer;
                  }
                }
              }
            }
          }
        }
      }
    }
    .has-two-item {
      .items-box {
        width: 46%;
      }
      .choose-active {
        color: #ffffff;
        background: #4b77be;
        border: none;
        cursor: auto;
        margin-bottom: 10px;
      }

      .list-item-content-box {
        width: 220px;
      }
    }
  }
}
